/* Global Styles */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    color: #333;
}

/* App Container */
.App {
    text-align: center;
    margin: 0 auto;
    padding: 20px;
}

.App-header {
    background-color: #282c34;
    color: white;
    padding: 10px;
    font-size: 18px;
}

/* Component Styling */
.DataUpload, .DataVisualization, .DataSummary, .Preprocessing {
    margin: 20px auto;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    background-color: white;
    width: 80%;
    max-width: 600px;
}

.section-title {
    font-size: 20px;
    margin-bottom: 15px;
    color: #333;
    font-weight: bold;
}

/* Drag and Drop Upload */
.drag-drop-area {
    border: 2px dashed #007bff;
    padding: 20px;
    margin: 10px 0;
    border-radius: 8px;
    color: #007bff;
    transition: background-color 0.3s;
}

.drag-drop-area:hover {
    background-color: #f4f4f4;
}

/* Buttons and Inputs */
button, input[type="file"] {
    cursor: pointer;
    padding: 10px 15px;
    margin: 10px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    transition: background-color 0.3s;
}

button:hover, button:focus {
    background-color: #0056b3;
}

input[type="file"] {
    background-color: #f4f4f4;
    color: #333;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* Form Elements */
label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

/* Loading and Messages */
.loading, .message {
    color: #007bff;
    font-style: italic;
}

/* Error Messages */
.error-message {
    color: #dc3545;
    font-weight: bold;
}

/* Footer */
footer {
    margin-top: 20px;
    padding: 20px;
    background-color: #282c34;
    color: white;
}

/* Responsive Design */
@media (max-width: 768px) {
    .DataUpload, .DataVisualization, .DataSummary, .Preprocessing {
        width: 90%;
    }
}
